// src/services/businessInfoService.js

import apiService from './apiService';
import { getBusinessInfo, saveBusinessInfo } from './storageService';
import eventBus from '../utils/eventBus';

// Constantes para la sincronizaci√≥n
const SYNC_INTERVAL = 30000; // 30 segundos
const CACHE_DURATION = 300000; // 5 minutos
let syncIntervalId = null;
let lastSyncTimestamp = 0;
let lastFetchTimestamp = 0;
let businessInfoCache = null;
let isInitialized = false;

/**
 * Sincroniza la informaci√≥n del negocio guardada en IndexedDB con el backend
 * @returns {Promise<Object>} - Resultado de la sincronizaci√≥n
 */
export async function syncBusinessInfoWithBackend() {
  try {
    // Obtener la informaci√≥n del negocio desde IndexedDB
    const businessInfo = await getBusinessInfo();
    
    if (!businessInfo || Object.keys(businessInfo).length === 0) {
      console.log('No hay informaci√≥n del negocio para sincronizar');
      return {
        success: false,
        message: 'No hay informaci√≥n del negocio para sincronizar'
      };
    }
    
    console.log('Sincronizando informaci√≥n del negocio con el backend:', businessInfo);
    
    // Asegurarse de que el token est√© configurado en apiService
    const token = localStorage.getItem('token');
    if (token) {
      apiService.setToken(token);
    }
    
    // Obtener el ID del restaurante del usuario actual
    const userResponse = await apiService.get('/auth/me');
    if (!userResponse || !userResponse.success || !userResponse.user || !userResponse.user.restaurante_id) {
      console.warn('No se pudo obtener el restaurante del usuario actual');
      return {
        success: false,
        message: 'No se pudo obtener el restaurante del usuario actual'
      };
    }
    
    const restauranteId = userResponse.user.restaurante_id;
    
    // Preparar los datos para actualizar el restaurante
    const restauranteData = {
      nombre: businessInfo.name,
      descripcion: businessInfo.description,
      direccion: businessInfo.address,
      telefono: businessInfo.contact,
      logo: businessInfo.logo,
      // Guardar la informaci√≥n de pago como un campo JSON
      informacion_pago: JSON.stringify(businessInfo.paymentInfo || {})
    };
    
    // Actualizar el restaurante en el backend
    const response = await apiService.put(`/restaurantes/${restauranteId}`, restauranteData);
    
    if (response && response.success) {
      console.log('Informaci√≥n del negocio sincronizada con √©xito');
      lastSyncTimestamp = Date.now();
      
      // Notificar a todos los componentes que la informaci√≥n ha sido sincronizada
      eventBus.emit('business-info-updated', businessInfo);
      
      return {
        success: true,
        message: 'Informaci√≥n del negocio sincronizada con √©xito',
        timestamp: lastSyncTimestamp
      };
    } else {
      console.warn('Error al sincronizar informaci√≥n del negocio:', response);
      return {
        success: false,
        message: response.message || 'Error al sincronizar informaci√≥n del negocio'
      };
    }
  } catch (error) {
    console.error('Error al sincronizar informaci√≥n del negocio:', error);
    return {
      success: false,
      message: error.message || 'Error al sincronizar informaci√≥n del negocio'
    };
  }
}

/**
 * Obtiene la informaci√≥n del negocio desde el backend y la guarda en IndexedDB
 * @param {boolean} forceUpdate - Forzar la actualizaci√≥n aunque est√© en cach√©
 * @returns {Promise<Object>} - Informaci√≥n del negocio
 */
export async function fetchBusinessInfoFromBackend(forceUpdate = false) {
  try {
    // Si tenemos informaci√≥n en cach√© y no ha pasado el tiempo de cach√©, usarla
    const now = Date.now();
    if (businessInfoCache && !forceUpdate && (now - lastFetchTimestamp) < CACHE_DURATION) {
      console.log('Usando informaci√≥n de negocio en cach√©');
      return businessInfoCache;
    }
    
    // Asegurarse de que el token est√© configurado en apiService
    const token = localStorage.getItem('token');
    if (token) {
      apiService.setToken(token);
    }
    
    // Obtener el ID del restaurante del usuario actual
    const userResponse = await apiService.get('/auth/me');
    if (!userResponse || !userResponse.success || !userResponse.user || !userResponse.user.restaurante_id) {
      console.warn('No se pudo obtener el restaurante del usuario actual');
      throw new Error('No se pudo obtener el restaurante del usuario actual');
    }
    
    const restauranteId = userResponse.user.restaurante_id;
    
    // Obtener el restaurante del backend
    const response = await apiService.get(`/restaurantes/${restauranteId}`);
    
    if (response && response.success && response.restaurante) {
      const restaurante = response.restaurante;
      
      // Convertir la informaci√≥n a formato de negocio
      let paymentInfo = {};
      try {
        if (restaurante.informacion_pago) {
          paymentInfo = JSON.parse(restaurante.informacion_pago);
        }
      } catch (e) {
        console.warn('Error al parsear informaci√≥n de pago:', e);
      }
      
      const businessInfo = {
        name: restaurante.nombre,
        description: restaurante.descripcion,
        address: restaurante.direccion,
        contact: restaurante.telefono,
        logo: restaurante.logo,
        paymentInfo
      };
      
      // Guardar en IndexedDB
      await saveBusinessInfo(businessInfo);
      
      // Actualizar cach√© y timestamp
      businessInfoCache = businessInfo;
      lastFetchTimestamp = now;
      
      // Notificar a todos los componentes que la informaci√≥n ha sido actualizada
      eventBus.emit('business-info-updated', businessInfo);
      
      console.log('Informaci√≥n del negocio obtenida con √©xito:', businessInfo);
      return businessInfo;
    } else {
      console.warn('Error al obtener informaci√≥n del negocio:', response);
      throw new Error(response?.message || 'Error al obtener informaci√≥n del negocio');
    }
  } catch (error) {
    console.error('Error al obtener informaci√≥n del negocio desde el backend:', error);
    
    // Si hay un error, intentar obtener la informaci√≥n desde IndexedDB
    try {
      const businessInfo = await getBusinessInfo();
      if (businessInfo) {
        return businessInfo;
      }
    } catch (localError) {
      console.error('Error al obtener informaci√≥n local:', localError);
    }
    
    throw error;
  }
}

/**
 * Actualiza la informaci√≥n del negocio en IndexedDB y la sincroniza con el backend
 * @param {Object} businessInfo - Nueva informaci√≥n del negocio
 * @returns {Promise<Object>} - Resultado de la actualizaci√≥n
 */
export async function updateBusinessInfo(businessInfo) {
  try {
    // Guardar en IndexedDB
    await saveBusinessInfo(businessInfo);
    
    // Actualizar cach√© y timestamp
    businessInfoCache = businessInfo;
    lastFetchTimestamp = Date.now();
    
    // Notificar a todos los componentes que la informaci√≥n ha sido actualizada
    eventBus.emit('business-info-updated', businessInfo);
    
    // Sincronizar con el backend
    const syncResult = await syncBusinessInfoWithBackend();
    
    return {
      success: true,
      message: 'Informaci√≥n del negocio actualizada con √©xito',
      syncResult
    };
  } catch (error) {
    console.error('Error al actualizar informaci√≥n del negocio:', error);
    return {
      success: false,
      message: error.message || 'Error al actualizar informaci√≥n del negocio'
    };
  }
}

/**
 * Verifica si hay cambios en la informaci√≥n del negocio
 * @returns {Promise<boolean>} - True si hay cambios, false si no
 */
export async function checkForBusinessInfoChanges() {
  try {
    // Obtener informaci√≥n actual desde IndexedDB
    const localInfo = await getBusinessInfo();
    
    // Obtener informaci√≥n desde el backend
    const backendInfo = await fetchBusinessInfoFromBackend(true);
    
    // Comparar ambas informaciones
    const localString = JSON.stringify(localInfo);
    const backendString = JSON.stringify(backendInfo);
    
    const hasChanges = localString !== backendString;
    
    if (hasChanges) {
      console.log('Se detectaron cambios en la informaci√≥n del negocio');
      
      // Actualizar la informaci√≥n local con la del backend
      await saveBusinessInfo(backendInfo);
      
      // Notificar a todos los componentes
      eventBus.emit('business-info-updated', backendInfo);
    }
    
    return hasChanges;
  } catch (error) {
    console.error('Error al verificar cambios en informaci√≥n del negocio:', error);
    return false;
  }
}

/**
 * Inicia la verificaci√≥n peri√≥dica de cambios en la informaci√≥n del negocio
 * @param {boolean} disableSync - Si es true, la sincronizaci√≥n se desactiva (√∫til para vistas compartidas)
 */
export function startBusinessInfoSyncInterval(disableSync = false) {
  // Si se solicita deshabilitar la sincronizaci√≥n (por ejemplo, en vistas compartidas), salir
  if (disableSync) {
    console.log('Sincronizaci√≥n de informaci√≥n del negocio desactivada (modo vista compartida)');
    return;
  }

  if (syncIntervalId) {
    clearInterval(syncIntervalId);
  }
  
  // Inicializar con una primera sincronizaci√≥n
  if (!isInitialized) {
    fetchBusinessInfoFromBackend()
      .catch(error => console.error('Error en sincronizaci√≥n inicial:', error));
    isInitialized = true;
  }
  
  syncIntervalId = setInterval(() => {
    checkForBusinessInfoChanges()
      .catch(error => console.error('Error en verificaci√≥n peri√≥dica:', error));
  }, SYNC_INTERVAL);
  
  console.log('Verificaci√≥n peri√≥dica de informaci√≥n del negocio iniciada');
}

/**
 * Detiene la verificaci√≥n peri√≥dica
 */
export function stopBusinessInfoSyncInterval() {
  if (syncIntervalId) {
    clearInterval(syncIntervalId);
    syncIntervalId = null;
    console.log('Verificaci√≥n peri√≥dica de informaci√≥n del negocio detenida');
  }
}

/**
 * Ejecuta la migraci√≥n para a√±adir la columna informacion_pago a la tabla restaurantes
 * @returns {Promise<Object>} - Resultado de la migraci√≥n
 */
export async function runAddInformacionPagoMigration() {
  try {
    // Asegurarse de que el token est√© configurado en apiService
    const token = localStorage.getItem('token');
    if (token) {
      apiService.setToken(token);
    }
    
    const response = await apiService.get('/restaurantes/migrate/add-informacion-pago');
    
    if (response && response.success) {
      console.log('Migraci√≥n ejecutada con √©xito:', response.message);
      return {
        success: true,
        message: response.message || 'Migraci√≥n ejecutada con √©xito'
      };
    } else {
      console.warn('Error al ejecutar migraci√≥n:', response);
      return {
        success: false,
        message: response.message || 'Error al ejecutar migraci√≥n'
      };
    }
  } catch (error) {
    console.error('Error al ejecutar migraci√≥n:', error);
    return {
      success: false,
      message: error.message || 'Error al ejecutar migraci√≥n'
    };
  }
}

/**
 * Limpia la cach√© y fuerza una nueva carga desde el backend
 */
export function invalidateBusinessInfoCache() {
  businessInfoCache = null;
  lastFetchTimestamp = 0;
}

export default {
  syncBusinessInfoWithBackend,
  fetchBusinessInfoFromBackend,
  updateBusinessInfo,
  checkForBusinessInfoChanges,
  startBusinessInfoSyncInterval,
  stopBusinessInfoSyncInterval,
  runAddInformacionPagoMigration,
  invalidateBusinessInfoCache
};
